import React, { useState ,useEffect} from 'react'
import '../../Static/Css/SideBar.css'
import default_Profile from '../../Static/Images/default_profile.png'
import { Link} from 'react-router-dom'
import profileImg from '../../Static/Images/profile.png'
import searchImg from '../../Static/Images/search.png'
import friendRequestImg from '../../Static/Images/friend_request.png'
import settingsImg from '../../Static/Images/settings.png'
import homeImg from '../../Static/Images/home.png'

function SideBar(props) {

  const[friendRequestAnim,setFriendRequestAnim]=useState(false)
  const[siderbarUsername,setSidebarUsername]=useState('')
  const[sidebarProfileString,setSidebarProfileString]=useState('null')

  useEffect(()=>{
    let token={
    token:localStorage.getItem('token')
  }
  fetch('api/verify', {
    method: 'POST',
    body: JSON.stringify(token),
    headers: { 'Content-Type': 'application/json' }
  }).then(res => res.json())
  .then((json)=>{
    setSidebarUsername(json.message.username)
    var requestOfEmail={
      email:json.message.email
    }

    fetch('api/friendRequestsAnimToggle', {
    method: 'POST',
    body: JSON.stringify(requestOfEmail),
    headers: { 'Content-Type': 'application/json' }
  }).then(res => res.json())
  .then((json)=>{
    if(json){
      setSidebarProfileString(json.profile)
    }
  })    

  })
  },[])
  console.log()

  useEffect(()=>{
    if(props.profileChange){
      setSidebarProfileString(props.profileChange)
    }
  },[props.profileChange])

  return (
    <div className='sidebar-cont'>
      <div className='sidebar-cont__inner'>
        <Link to='/profile'>
        <div className='user-avatar'>
          <img className='user-avatar__avatar' src={sidebarProfileString == 'null' ? default_Profile : sidebarProfileString } />
          <p className='user-avatar__username'>@{siderbarUsername}</p>
        </div>
        </Link>
        <div className='sidebar-links'>
        <Link to='/'><img  className='sidebar-links__img sidebar-links__img1' src={homeImg} /></Link> 
          <Link to='/profile'><img className='sidebar-links__img'  src={profileImg} /></Link> 
          <Link to='/search'><img className='sidebar-links__img'  src={searchImg} /></Link> 
          <Link onClick={()=>{setFriendRequestAnim(false)}} to='/friendrequests'><img className={friendRequestAnim ? 'sidebar-links__img logo-anim__true':'sidebar-links__img'}  src={friendRequestImg} /></Link> 
          <Link to='/settings'><img  className='sidebar-links__img' src={settingsImg} /></Link> 
        </div>
      </div>
    </div>
  )
}

export default SideBar