import React, { useState, useEffect } from 'react'
import '../../Static/Css/FriendRequests.css'
import noFriends from '../../Static/Images/noFriends.png'
import default_profile from '../../Static/Images/default_profile.png'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function FriendRequests() {

    const[friendRequests,setFriendRequests]=useState([])
    const[usernameForFetch,setUsernameForFetch]=useState('')
    const[getFriendReqAnim,setgetFriendReqAnim]=useState(false)

    useEffect(()=>{
        var token={
          token:localStorage.getItem('token')}
    
        fetch('api/verify', {
          method: 'POST',
          body: JSON.stringify(token),
          headers: { 'Content-Type': 'application/json' }
        }).then(res => res.json())
          .then((json)=>{
            setUsernameForFetch(json.message.username)
        var getFriendRequests={
          email:json.message.email
        }
        fetch('api/friendrequests', {
          method: 'POST',
          body: JSON.stringify(getFriendRequests),
          headers: { 'Content-Type': 'application/json' }
        }).then(res => res.json())
          .then((json)=>{
          toast.success('revceived')
          setFriendRequests([json.message.friendRequests])
          // setgetFriendReqAnim(true)

          const test=setInterval(()=>{
            setgetFriendReqAnim(true)
            clearInterval(test)
          },300)
          })
        })
      },[])

      function acceptRequestHandler(e){
        var requestDetails={
          acceptedRequestUsername:e.target.id,
          usernameFrom:usernameForFetch
        }
        fetch('api/acceptFriendRequest', {
          method: 'POST',
          body: JSON.stringify(requestDetails),
          headers: { 'Content-Type': 'application/json' }
        }).then(res => res.json())
          .then((json)=>{
            var filter_friendRequests=friendRequests[0].filter((obj)=>{
              return obj.username != e.target.id
            })
            setFriendRequests([filter_friendRequests])
          })

      }
  return (
    <>
    <div className='friendRec-cont'>
        <div className='friendRec-cont__inner'>
            <div className='friendRec-results'>
              {getFriendReqAnim ? friendRequests[0].length>0 ?friendRequests[0].map((ele,index)=>(
                <div key={index} className='friendRec-result-item'>
                    <img src={ele.profile == 'null' ? default_profile : ele.profile}/>
                    <div className='friendRec-result-item-logo'>
                        <p>{ele.username}</p>
                        <button className='friendRec-result-item__acceptReq-btn' id={ele.username} onClick={acceptRequestHandler}>Accept</button>
                    </div>
                </div>)) :<div className='friendRec-cont__empty'>

                    <div className='friendRec-cont__empty-inner'>
                      <img src={noFriends} />
                      <p>Sorry, You have no requests</p>
                    </div>
                </div> :<div className="card">
                      <div className="skel card__image"></div>
                      <div className='card-titleDes'>
                        <div className="skel card__title"></div>
                        <div className="skel card__description"></div>
                      </div>
                    </div>
              }
            </div>
        </div>
        <ToastContainer />
    </div>
    </>

  )
}

export default FriendRequests