import React, { useState } from 'react'
import Blog from './Blog'
import FriendsBar from './FriendsBar'

function FriendsBlogCont() {

    const[friendUpdate,setFriendUpdate]=useState(null)
    const[userDataForConts,setUserDataForConts]=useState('ThoughtsWorld')

    function funcForRetrieveFriend(e){
        setFriendUpdate(e)
      }

    useState(()=>{
        var token={
          token:localStorage.getItem('token')}

        fetch('api/verify', {
          method: 'POST',
          body: JSON.stringify(token),
          headers: { 'Content-Type': 'application/json' }
        }).then(res => res.json())
          .then((json)=>{
            setUserDataForConts(json.message.username)

          })
    },[])

  return (
    <React.Fragment>
        <FriendsBar funcForRetrieveFriend={funcForRetrieveFriend} userData={userDataForConts} />
        <Blog funcForSendingFriend={friendUpdate} userData={userDataForConts} />
    </React.Fragment>
  )
}

export default FriendsBlogCont