import React, { useEffect, useState } from 'react'
import '../../Static/Css/Blog.css'
import default_profile from '../../Static/Images/default_profile.png'

function Blog(props) {

  const[loadingToggle,setLoadingToggle]=useState(false)
  const [blogDataObj,setBlogDataObj]=useState([])
  const[userPerData,setUserPerData]=useState([])

  useEffect(()=>{
    var usernameObj={username:'ThoughtsWorld'}
    if(props.funcForSendingFriend){
      usernameObj={username:props.funcForSendingFriend}
      setLoadingToggle(false)
    }

    fetch('api/blogtweets', {
      method: 'POST',
      body: JSON.stringify(usernameObj),
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json())
      .then((json)=>{
        setBlogDataObj(json.thoughts)
        setUserPerData(json.userPersonalData)
        const iner=setInterval(()=>{
          setLoadingToggle(true)
          clearInterval(iner)
        },300)
      })
  },[props.funcForSendingFriend])

  // {props.funcForSendingFriend != null && fetchBlogTweets()}

  // function fetchBlogTweets(){
  //   var usernameObj={username:props.funcForSendingFriend}
  //   fetch('api/blogtweets', {
  //     method: 'POST',
  //     body: JSON.stringify(usernameObj),
  //     headers: { 'Content-Type': 'application/json' }
  //   }).then(res => res.json())
  //     .then((json)=>{
  //       setBlogDataObj(json.thoughts)
  //     })
  // }

  return (
    <div className='blog-cont'>
      <div className='blog-cont__inner'>
        <div className='blog-avatar-cont'>
          <div>
            {loadingToggle ? <><img src={userPerData[1] == 'null' ? default_profile : userPerData[1] }/>
            <p>{userPerData[0]}</p> </> : <><div className='blogdLoading-image blogSkel'></div>
            <p className='blogdLoading-username blogSkel'></p></> }
          </div>
          {loadingToggle ? <p className='quote'>{userPerData[2]}</p> :
          <p className='bloadLoading-quote blogSkel'></p>}
        </div>
        <div className='blog-item-cont'>
          {loadingToggle ?  blogDataObj.map((e,ind)=>(
            <div key={ind} className='blog-item'>
            <div className='blog-item-tweet'><p>{e.tweet}</p></div><br></br>
            <div className='blog-item-time'><p>{e.time}</p></div>
          </div>
          )) :
          <div className='blogLoading-cont'>
            <div className='blogLoading-tweet blogSkel'></div>
            <div className='blogLoading-time'><p className='blogSkel'></p></div>
          </div>}
        </div>
      </div>
    </div>
  )
}

export default Blog