import React,{useEffect, useState} from 'react'
import '../../Static/Css/Profile.css'
import defaultProfile from '../../Static/Images/default_profile.png'

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Profile(props) {
  const[write,setWrite]=useState('')
  const[thoughtsCont,setThoughtsCont]=useState([])
  const[userEmailHolder,setUserEmailHolder]=useState('')
  // const[thoughtsLoader,setThoughtsLoader]=useState(false)

  function writeTho(e){
    setWrite(e.target.value)
  }

  // function editProfileHandler(e){
  //   function base64(file){
  //     return new Promise((resolve,reject)=>{
  //       const reader=new FileReader()
  //       reader.readAsDataURL(file)
  //       reader.onload=()=>resolve(reader.result)
  //       reader.onerror=error=>reject(error)
  //     })
  //   }
  //   base64(e.target.files[0]).then(data=>console.log(data))
  // }

  useEffect(()=>{
    var token={
      token:localStorage.getItem('token')}

    fetch('api/verify', {
      method: 'POST',
      body: JSON.stringify(token),
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json())
      .then((json)=>{
        setUserEmailHolder(json.message.email)
    var getThoughts={
      email:json.message.email
    }
    fetch('api/thoughtsSender', {
      method: 'POST',
      body: JSON.stringify(getThoughts),
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json())
      .then((json)=>{
      toast.success('revceived')
      setThoughtsCont(json[0].thoughts)
      })
    })
  },[])


  function thoughtFetchHandler(){
    if(write.length<1){
      toast("Thought's will never be EMPTY")
    }
    else{
      const d = new Date()
      
      let hr=d.getHours()
      let min=d.getMinutes()
      let noon='AM'
      hr>=12 ? noon='PM':noon='AM'
      hr>12 ? hr=hr-12:hr=hr-0
      hr<10 ? hr='0'+hr :hr=hr

      let dd_mm_yy=d.toLocaleDateString('pt-PT')

        let thought={
          email:userEmailHolder,
          thought:{tweet:write,time:hr+':'+min+' '+noon+' '+dd_mm_yy}
      }
      fetch('api/thoughtColl', {
        method: 'POST',
        body: JSON.stringify(thought),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
      .then((json)=>{
        toast.success('Thought Added')
        setThoughtsCont(json.someValue[0].thoughts)
        })
    }
  }

  return (
    <div className='profile-cont'>
    <div className='profile-cont__inner'>
      <div className='profile-thought-cont'>
        <div className='sendThought-cont'>
          <textarea placeholder='Write Your Thought' cols='50' rows='4' onChange={writeTho} ></textarea>
        </div>
        <div>
          <button onClick={thoughtFetchHandler}>Send Thought</button>
          <button>Cancel</button>
        </div>
      </div>
      <div className='blog-item-cont'>
        {thoughtsCont.map((e,ind)=>(
        <div key={ind} className='blog-item'>
          <div className='blog-item-tweet'><p>{e.tweet}</p></div><br></br>
          <div className='blog-item-time'><p>{e.time}</p></div>
        </div>
        ))}
      </div>
    </div>
    <ToastContainer />
  </div>
  )
}

export default Profile