import React, { useRef, useState ,useEffect} from 'react'
import '../../Static/Css/Search.css'
import default_profile from '../../Static/Images/default_profile.png'

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Search() {
    const[searchResult,setSearchResult]=useState([])
    const inputRef=useRef(null)
    const[MainUsername,setMainUsername]=useState('')
    const[sendRequestBtn,setSendRequestBtn]=useState(6)

    useEffect(()=>{
      var token={
        token:localStorage.getItem('token')}
  
      fetch('api/verify', {
        method: 'POST',
        body: JSON.stringify(token),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
        .then((json)=>{
          setMainUsername(json.message.username)
      })
    },[])

    function SearchFetchHandler(){
        if(inputRef.current.value.length<1){
          toast("Thought's will never be EMPTY")
        }
        else{ 
          if(inputRef.current.value == MainUsername){
            toast('Its your username')
          }else{         
              var getSearchResults={
                usernameTo:inputRef.current.value,
                usernameFrom:MainUsername
              }
              fetch('api/searcher', {
                method: 'POST',
                body: JSON.stringify(getSearchResults),
                headers: { 'Content-Type': 'application/json' }
              }).then(res => res.json())
                .then((json)=>{
                  if(json.message){
                    setSearchResult([{username:json.message.username,profile:json.message.profile}])
                    setSendRequestBtn(json.message.RequestStatus.SentReqStatus)
                  }
                  else{
                    toast.error('No user found')
                  }
              })
        }}
      }

      function sendRequestHandler(){
        let userForToRequest={
          usernameTo:searchResult[0].username,
          usernameFrom:MainUsername
        }
        fetch('api/sendRequests', {
                method: 'POST',
                body: JSON.stringify(userForToRequest),
                headers: { 'Content-Type': 'application/json' }
              }).then(res => res.json())
                .then((json)=>{
                  setSendRequestBtn(2)           
              })
      }

  return (
    <div className='search-cont'>
        <div className='search-cont__inner'>
            <div className='search-inp'>
                <div>
                    <input placeholder='Search' type='text' ref={inputRef}/>
                    <button onClick={SearchFetchHandler}>search</button>
                </div>
            </div>
            <div className='empty-line'></div>
            <div className='search-results'>
                {searchResult.map((ele,index)=>(
                <div key={index} className='search-result-item'>
                    <img src={ele.profile == 'null' ? default_profile : ele.profile}/>
                    <div className='search-result-item-logo'>
                        <p>{ele.username}</p>
                        {sendRequestBtn ? 
                        sendRequestBtn == 1?<button className='search-result-alreadyFriends'>Already friends</button> :<button className='search-result-requestSent'> Request sent</button> :
                        <button className='search-result-sendRequest' onClick={sendRequestHandler}>Send Request</button> 
                        }
                    </div>
                </div>
                ))}
            </div>
        </div>
        <ToastContainer />
    </div>
  )
}

export default Search