import React, { useEffect, useState } from 'react'
import SideBar from './SideBar'
import FriendsBar from './FriendsBar'
import NavBar from './NavBar'
import Blog from './Blog'
import '../../Static/Css/Home.css'
import {BrowserRouter as Router, Route, Routes, useNavigate} from 'react-router-dom'
import Search from '../NavLinks/Search'
import Profile from '../NavLinks/Profile'
import FriendRequests from '../NavLinks/FriendRequests'
import FriendsBlogCont from './FriendsBlogCont'
import ProfileEditor from './ProfileEditor'
import Settings from '../NavLinks/Settings'
import Error404 from '../NavLinks/Error404'


function Home() {

  const navigate=useNavigate()

  const[display,setDisplay]=useState(false)
  const[profileChange,setProfileChange]=useState(null)

  function updatedProfile(e){
    setProfileChange(e)
  }

    useEffect(()=>{
    if(localStorage.getItem('token')){
      let token={
        token:localStorage.getItem('token')
      }
      fetch('api/verify', {
        method: 'POST',
        body: JSON.stringify(token),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
      .then((json)=>{
        if(json.message){
          setDisplay(true)
        }
        else{
          navigate('/login')
        }
      })
      }
      else{
        navigate('/login')
      }
  },[])

  return (
    <>
    {display && <div className='home-cont'>
      <div className='home__navbar-cont'>
        <NavBar />
      </div>
      <div className='home__body-cont'>
        <div className='home__body-cont__inner'>
          <SideBar profileChange={profileChange}/>
          <Routes>
              <Route path='/' element={(
                <>
                <FriendsBlogCont />
                </>
              )}/>
              <Route path='/search' element={<Search />} />
              <Route path='/profile' element={<Profile />} />
              <Route path='/friendrequests' element={<FriendRequests />} />
              <Route path='/settings' element={<Settings />} />
              <Route path='/updateProfile' element={<ProfileEditor updatedProfile={updatedProfile} />} />
              <Route path='*' element={<Error404 />} />
          </Routes>
        </div>
      </div>
    </div>}
    </>
  )
}

export default Home