import React, { useState } from 'react'
import '../../Static/Css/NavBar.css'
import titleImg from '../../Static/Images/title.png'
import { useNavigate } from 'react-router-dom'

function NavBar() {
  const navigate=useNavigate()
  const[logoutDialog,setLogoutDialog]=useState(true)

  function logoutToggle(){
    setLogoutDialog(!logoutDialog)
  }

  function logoutSure(){
    localStorage.removeItem('token')
    navigate('/login')
  }
  return (
    <div className='navbar-cont'>
      <div className='navbar-cont__inner'>
        <div className='title-cont'>
          <p className='navbar-title'>THOUGHT'S OUT</p>
          <img src={titleImg}/>
        </div>
        <div className='logout-cont'>
          <button onClick={logoutToggle} className={logoutDialog?'logout-btn':'logout-btn__false'}>Logout</button>
          <button onClick={logoutSure} className={logoutDialog? 'logout-conf-btn__false':'logout-conf-btn1'}>Sure</button>
          <button onClick={logoutToggle} className={logoutDialog? 'logout-conf-btn__false':'logout-conf-btn2'}>No</button>
        </div>
      </div>
    </div>
  )
}

export default NavBar