import React, { useEffect, useState } from 'react'
import '../../Static/Css/Login.css'
import { Icon } from 'react-icons-kit'
import {view_off} from 'react-icons-kit/ikons/view_off'
import {view} from 'react-icons-kit/ikons/view'
import { Link, useNavigate } from 'react-router-dom'

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Login() {

  const navigate=useNavigate()
  const [emailCollector,setEmailCollector]=useState('')
  const [passwordCollector,setPasswordCollector]=useState('')
  const [showPasswordToggle,setShowPasswordToggle]=useState(view)
  const [type,setType]=useState('password')

  function toastMess(message){
    toast.error(message,{
    position: "top-center",
    autoClose: 2000
  })
  }
  function toastSuccess(message){
    toast.success(message,{
    position: "top-center",
    autoClose: 2000
  })
  }
  useEffect(()=>{
    if(localStorage.getItem('token')){
      let token={
        token:localStorage.getItem('token')
      }
      fetch('api/verify', {
        method: 'POST',
        body: JSON.stringify(token),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
      .then((json)=>{
        if(json.message){
          navigate('/')
        }
      })
      }
  },[]) 

      function passwordToggleHandler(){
        if(type=='password'){
          setType('text')
          setShowPasswordToggle(view_off)
        }else{
          setType('password')
          setShowPasswordToggle(view)    
        }
      }

      function loginHandler(){
        if(emailCollector.length===0 || passwordCollector.length ===0){
          toastMess('Fill the fields')
        }else{
          if (!/\S+@\S+\.\S+/.test(emailCollector)){
            toastMess('Invalid email form')
          }
          else{
            if(passwordCollector.length<6){
              toastMess('Password is short')
            }else{
              fetch('api/login', {
                method: 'POST',
                body: JSON.stringify({email:emailCollector,password:passwordCollector}),
                headers: { 'Content-Type': 'application/json' }
                }).then(res => res.json())
                .then((json)=>{
                  if(json.message.authToken){
                    localStorage.setItem('token',json.message.authToken)
                    toastSuccess('successfully loged in')
                    navigate('/')
                  }
                  else{
                    toastMess(json.message)
                  }
                })
            }
          }
        }
      }

  return (
    <div className="login-cont">
      <div className='login-cont__inner'>
        <div>
          <p className='login-title'>Log In</p>
        </div>
        <div>
          <input type='text'placeholder='email' onChange={(e)=>setEmailCollector(e.target.value)}/>
        </div>
        <div>
          <input type={type} placeholder='password' onChange={(e)=>setPasswordCollector(e.target.value)}/>
          <span onClick={passwordToggleHandler} className='showPasswordToggle'>
            <Icon icon={showPasswordToggle}/>
          </span>
        </div>
        <div className='login-btns'>
          <button onClick={loginHandler}>Log In</button>
          <button><Link style={{textDecoration: 'none',color:'black'}} to='/signup'>Create An Account</Link></button>
        </div>
      </div>
      <ToastContainer />
    </div>
  )
}

export default Login