import React, { useState ,useEffect} from 'react'
import '../../Static/Css/FriendsBar.css'

function FriendsBar(props) {

  const [friendsList,setFriendsList]=useState([])

  useEffect(()=>{
      var token={
        token:localStorage.getItem('token')}
  
      fetch('api/verify', {
        method: 'POST',
        body: JSON.stringify(token),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
        .then((json)=>{

          var emailForFriendslist={email:json.message.email}

          fetch('api/friendslist', {
            method: 'POST',
            body: JSON.stringify(emailForFriendslist),
            headers: { 'Content-Type': 'application/json' }
          }).then(res => res.json())
            .then((json)=>{
              setFriendsList(json.message.friends)

          })

      })
  },[])

  function friendItemClickHandler(e){
    props.funcForRetrieveFriend(e.target.id)
  }

  return (
    <div className='friendsbar-cont'>
        <div className='friends-title-cont'>
          <p className='friends-title'>Friends List</p>
        </div>
        <div className='friends-item-cont'>
          {friendsList.map((e,ind)=><span onClick={friendItemClickHandler} key={ind} id={e} className='friends-item'>{e}</span>)}
        </div>
    </div>
  )
}

export default FriendsBar