import React, { useEffect, useState } from 'react'
import '../../Static/Css/Signup.css'
import loadingGif from '../../Static/Images/Spinner.gif'
import tickMark from '../../Static/Images/tickmark.png'
import { Icon } from 'react-icons-kit'
import {view_off} from 'react-icons-kit/ikons/view_off'
import {view} from 'react-icons-kit/ikons/view'
import { Link, useNavigate } from 'react-router-dom'

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Signup() {

  const navigate=useNavigate()
  const [emailCollector,setEmailCollector]=useState('')
  const [passwordCollector,setPasswordCollector]=useState('')
  const [usernameCollector,setUsernameCollector]=useState('')
  const [otpCollector,setOtpCollector]=useState('')
  const [showPasswordToggle,setShowPasswordToggle]=useState(view)
  const [type,setType]=useState('password')

  const [loadingAnimation,setLoadingAnimation]=useState(false)
  const [enterOtp,setEnterOtp]=useState(false)

  function toastError(message){
    toast.error(message,{
    position: "top-center",
    autoClose: 2000
  })
}
  function toastSuccess(message){
    toast.success(message,{
    position: "top-center",
    autoClose: 2000
  })
  }

  useEffect(()=>{
    if(localStorage.getItem('token')){
      let token={
        token:localStorage.getItem('token')
      }
      fetch('api/verify', {
        method: 'POST',
        body: JSON.stringify(token),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
      .then((json)=>{
        if(json.message){
          navigate('/')
        }
      })
      }
  },[]) 

      function passwordToggleHandler(){
        if(type=='password'){
          setType('text')
          setShowPasswordToggle(view_off)
        }else{
          setType('password')
          setShowPasswordToggle(view)    
        }
      }

      function signupHandler(){

        if(emailCollector.length===0 || passwordCollector.length ===0 || usernameCollector.length ===0){
          toastError('Fill the fields')
        }else{
          if (!/\S+@\S+\.\S+/.test(emailCollector)){
            toastError('Invalid email form')
          }
          else{
            if(passwordCollector.length<6){
              toastError('Password is short')
            }else{
              if(usernameCollector.length <= 4 || usernameCollector.length >= 10 ){
                toastError('Username must with 5 - 9 Char')
              } 
              else{
                if(usernameCollector.replace(' ','') != usernameCollector){
                  toastError('Remove space in username') 
                }
                else{
                  // setLoadingAnimation(true)
                  fetch('api/usernameAndMailFinder', {
                    method: 'POST',
                    body: JSON.stringify({username:usernameCollector,email:emailCollector}),
                    headers: { 'Content-Type': 'application/json' }
                    }).then(res => res.json())
                    .then((json)=>{
                      if(json.emailFound || json.usernameFound){
                        // setLoadingAnimation(false)
                        if(json.emailFound){
                          toastError('Email is already taken')
                        }
                        if(json.usernameFound){
                          toastError('Username is already taken')
                        }
                      }
                      else{
                        setLoadingAnimation(true)
                        fetch('api/setmailotp', {
                          method: 'POST',
                          body: JSON.stringify({email:emailCollector}),
                          headers: { 'Content-Type': 'application/json' }
                          }).then(res => res.json())
                          .then((json)=>{
                            setEnterOtp(true)
                          })
                      }
                    })
                }
              }
            }
          }
        }
      }

      function otpValidationHandler(){
        fetch('api/otpValidation', {
          method: 'POST',
          body: JSON.stringify({email:emailCollector,otp:otpCollector}),
          headers: { 'Content-Type': 'application/json' }
          }).then(res => res.json())
        .then((json)=>{
          if(json.message){
            toastSuccess('valid OTP')
            toast('Please wait',{
              position:'top-center',
            })
            fetch('api/signup', {
              method: 'POST',
              body: JSON.stringify({email:emailCollector,username:usernameCollector,password:passwordCollector}),
              headers: { 'Content-Type': 'application/json' }
              }).then(res => res.json())
            .then((json)=>{
              if(json.authToken){
                localStorage.setItem('token',json.authToken)
                toastSuccess('Account is sucessfull created')
                navigate('/updateprofile')
              }
            })
          }
          else{
            toastError('Invalid OTP')
          }
        })
      }

  return (
    <div className="signup-cont">
      <div className={ enterOtp ? 'signup-cont__inner otpLoading-cont' :'signup-cont__inner'}>
        {loadingAnimation ? enterOtp ? <div className='otpLoading-cont__inner'>
           <div><img className='otpLoading-cont__tickmark' src={tickMark}/></div>
           <div>Please Check mail</div>
           <div>And enter OTP</div>
           </div>:<div><img className='otpLoading-cont__image' src={loadingGif}/></div>:<>
        <div>
          <p className='signup-title'>Sign Up</p>
        </div>
        <div>
          <input type='text'placeholder='email' onChange={(e)=>setEmailCollector(e.target.value)}/>
        </div>
        <div>
          <input type='text'placeholder='username                                        
                  *unique' onChange={(e)=>setUsernameCollector(e.target.value)}/>
        </div>
        <div>
          <input type={type} placeholder='password' onChange={(e)=>setPasswordCollector(e.target.value)}/>
          <span onClick={passwordToggleHandler} className='showPasswordToggle'>
            <Icon icon={showPasswordToggle}/>
          </span>
        </div>
        <div className='signup-btns'>
          <button onClick={signupHandler}>Sign Up</button>
          <button><Link style={{textDecoration: 'none',color:'black'}} to='/login'>Already have account</Link></button>
        </div> </> }
      </div>

     {enterOtp &&  <div className='otp-cont'>
        <input type='text' placeholder='enter otp' onChange={(e)=>setOtpCollector(e.target.value)}/>
        <button onClick={otpValidationHandler}>Submit</button>
      </div> }
      <ToastContainer />
    </div>
  )
}

export default Signup