import React from "react";
import Avatar from "react-avatar-edit";
import default_profile from '../../Static/Images/default_profile.png'
import arrowLeft from '../../Static/Images/arrow-left.svg'
import '../../Static/Css/ProfileEditor.css'
import { useNavigate } from 'react-router-dom'
import { useState, useEffect } from "react";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function ProfileEditor(props) {

  const navigate=useNavigate()
  const [captionValue,setCaptionValue]=useState('')
  const [preview, setPreview] = useState(null);
  const [profileChanges,setProfileChanges]=useState(false)
  const [captionChanges,setCaptionChanges]=useState(false)
  const [mailHolder,setmailHolder]=useState('')

  function toastError(message){
    toast.error(message,{
    position: "top-center",
    autoClose: 2000
  })
}
  function toastSuccess(message){
    toast.success(message,{
    position: "top-center",
    autoClose: 2000
  })
  }


  useEffect(()=>{
    var token={
      token:localStorage.getItem('token')}

    fetch('api/verify', {
      method: 'POST',
      body: JSON.stringify(token),
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json())
      .then((json)=>{
      setmailHolder(json.message.email)
      var dataForCaption={email:json.message.email}

    fetch('api/getProfileCaption', {
      method: 'POST',
      body: JSON.stringify(dataForCaption),
      headers: { 'Content-Type': 'application/json' }
    }).then(res => res.json())
      .then((json)=>{
        setCaptionValue(json.caption)
        if(json.profile != 'null'){
          setPreview(json.profile)
        }
      })
    })
  },[])


  function onCrop(pv) {
    setPreview(pv);
    setProfileChanges(true)
  }
  function onBeforeFileLoad(elem) {
    if (elem.target.files[0].size > 200000) {
      alert("File is too big!");
      elem.target.value = "";
    }
  }
  function tryCaption(e){
    if(e.target.localName=='p'){
      setCaptionValue(e.target.textContent)
      captionChangeHandler()
    }
  }

  function captionChangeHandler(e){
    if(e){
      setCaptionValue(e.target.value)
    }
    setCaptionChanges(true)
  }

  function profileChangesHandler(){
    if(profileChanges){
      fetch('api/updateprofile', {
        method: 'POST',
        body: JSON.stringify({email:mailHolder,profile:preview}),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
        .then((json)=>{
          if(json.message){
            toastSuccess('Profile updated sucessfully')
            props.updatedProfile(preview)
          }else{
            toastError('unable to process your request')
          }
        })
    }
    else{
      toastError('please make some changes')
    }
  }

  function captionChangesHandler(){
    if(captionChanges){
      fetch('api/updatecaption', {
        method: 'POST',
        body: JSON.stringify({email:mailHolder,caption:captionValue}),
        headers: { 'Content-Type': 'application/json' }
      }).then(res => res.json())
        .then((json)=>{
          if(json.message){
            toastSuccess('caption updated sucessfully')
          }else{
            toastError('unable to process your request')
          }
        })
    }
    else{
      toastError('please make some changes')
    }
  }

  return (
    <div className="ProfileEditor-cont">
      <div className="ProfileEditor-cont__inner">
      <img onClick={()=>navigate('/settings')} className="arrow_left" src={arrowLeft}/><br></br>
      <div className="profileEdit-leftRight-holder">
        <div className="profileEdit-leftcont">
          <div className="profile-previewImage">
            <span>Profile Preview</span>
            <img src={preview ? preview : default_profile} alt="Preview" />
          </div>
          <div>
            <Avatar
              width={230}
              height={230}
              onCrop={onCrop}
              // onClose={onClose}
              onBeforeFileLoad={onBeforeFileLoad}
              src={null}
            />
          </div>
        </div>  
        <div className="profileEdit-rightcont">
          <p>Caption</p>
          <textarea cols='50' rows='4' value={captionValue} onChange={captionChangeHandler}></textarea>
          <div className="try-captions">
            <p>Try this..</p>
            <div onClick={tryCaption}>
              <p>Stress less and enjoy the best</p>
              <p>“Be yourself, there's no one better</p>
              <p>Chillin' with my bros</p>
              <p>Haters are my greatest motivators</p>
              <p>Whatever is good for your soul, do that</p>
            </div>
          </div>
        </div>
      </div> 
      <div className="saveChanges-btn">
        <button onClick={profileChangesHandler}>Save Profile Changes</button>
        <button onClick={captionChangesHandler}>Save Caption Changes</button>
      </div>
      </div>
      <ToastContainer />
    </div>
  );

}

export default ProfileEditor